<template>
<div>
          <div class="maintop">
            <div class="maintopleft">
              <div>
                <span>下午好，<a href="javascript:;">子君</a></span>
              </div>
              <div>
                <i class="el-icon-message" style="color:#EEC56D"></i>
                邮件：{{recivevalue}}封
                <a href="javascript:;">未读邮件</a>
              </div>
              <div>
                <a href="javascript:;">收件箱({{recivevalue}})</a>
                <a href="javascript:;">订阅中心({{subscribevalue}})</a>
              </div>
            </div>
            <div class="maintopright">
              <van-icon name="qr" size="50px" @click="showvx"/>
              <span>阿随笔记</span>
              <img v-show="vxflag" src="../assets/myvx.jpg" alt="">
            </div>
          </div>
          <div class="mainbottom">
            <div class="mainbottomleft">
              <div class="topbox">邮箱推荐</div>
              <div class="middlebox">
                <img src="https://rescdn.qqmail.com/zh_CN/htmledition/images/today_icos/i_notes1e9c5d.gif" alt="">
                <div>给邮件添加备注</div>
                <span>读信时，记下备忘方便日后查阅。<a href="javascript:;">了解详情</a></span>
              </div>
              <div class="bottombox">
                <ul>
                  <li>
                    <i class="el-icon-apple"></i>
                    <a href="javascript:;">苹果版</a>
                  </li>
                  <li>
                    <i class="el-icon-star-off"></i>
                    <a href="javascript:;">明信片</a><span> -</span>
                    <a href="javascript:;">贺卡</a>
                  </li>
                  <li>
                    <i class="el-icon-pear"></i>
                    <a href="javascript:;">梨子版</a>
                  </li>
                  <li>
                    <i class="el-icon-goods"></i>
                    <a href="javascript:;">记事本</a><span> -</span>
                    <a href="javascript:;">日历</a>
                  </li>
                  <li>
                    <i class="el-icon-grape"></i>
                    <a href="javascript:;">葡萄版</a>
                  </li>
                  <li>
                    <i class="el-icon-refresh"></i>
                    <a href="javascript:;">文件中转站</a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="mainbottomright">
              <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="我的技能" name="first">
                  <div class="skillbox">
                    <div>1.精通HTML5+CSS3</div>
                    <div>2.熟悉div布局</div>
                    <div>3.熟练使用Vue框架</div>
                    <div>4.搭配Vant和Element库</div>
                    <div>5.极强的学习能力</div>
                  </div>
                </el-tab-pane>
                <el-tab-pane label="我的信息" name="second">
                  <div class="skillbox">
                    <div>性别：男 <i class="el-icon-check"></i></div>
                    <div>年龄：22</div>
                    <div>我的专业：软件工程</div>
                    <div>毕业年限：2021应届毕业生</div>
                    <div>我的学校：重庆邮电大学</div>
                  </div>
                </el-tab-pane>
              </el-tabs>
            </div>
          </div>
          <div class="footer">
              <ul>
                <li :width="screenWidth">暖灯行动<span>|</span></li>
                <li>企业邮箱<span>|</span></li>
                <li>开放平台<span>|</span></li>
                <li>体验室<span>|</span></li>
                <li>邮箱助手<span>|</span></li>
                <li>互助查询<span>|</span></li>
                <li>隐私政策</li>
              </ul>
            </div>
        </div>
</template>
<script>
export default {
    name:'mymain',
    data() {
        return {
            vxflag:false,
            activeName: 'first',
            recivevalue:10,
            rubbishvalue:12,
            subscribevalue:2,
            screenWidth: document.body.clientWidth,
        }
    },
    methods:{
    showvx:function(){
      this.vxflag = !this.vxflag;
    },
    handleClick(tab, event) {
        
    }
  },
  mounted () {
       const that = this
       window.onresize = () => {
           return (() => {
               window.screenWidth = document.bodclientWidth
               that.screenWidth = window.screenWidth
           })()
       }
   },
  watch: {
      screenWidth (val) {
        var e1 = document.querySelector('.maintopright');
        var e2 = document.querySelector('.mainbottomright')
        e1.style.display = 'none';
        e2.style.display = 'none';
      }
  },
}
</script>
<style scoped>
.maintop{
  height: 1.5rem;
  width: 100%;
  position: relative;
}
.maintopleft{
  padding: .1rem;
}
.maintopleft div{
  width: 100%;
  font-size: .18rem;
}
a{
  color: #1E5494;
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
.maintopleft div:nth-child(1),
.maintopleft div:nth-child(3){
  font-weight: bold;
  height: .3rem;
}
.maintopleft div:nth-child(2){
  font-size: .14rem;
  height: .3rem;
}
.maintopleft div:nth-child(3){
  padding-left: 12px;
  font-size: .16rem;
}
.maintopleft div:nth-child(3)>a{
  margin-left: 5px;
}
.maintopright{
  display: block;
  position: absolute;
  height: .7rem;
  width: 2.2rem!important;
  background-color: #E0ECF9;
  bottom: 0;
  right: 29px;
  border-radius: 5px;
  font-size: .2rem;
  padding: .1rem 0 0 .1rem;
  box-sizing: border-box;
}
.maintopright span{
  line-height: .7rem;
  position: absolute;
  top: 0;
  left: .7rem;
}
.maintopright img{
  display: block;
  height: 100px;
  width: 100px;
  position: absolute;
  top: 0;
  left: 70px;;
  z-index: 99;
}
.mainbottom{
  padding: .2rem 0 0 .1rem;
  position: relative;
}
.mainbottomleft{
  width: 80%;
  height: 2rem;
  border: 1px solid #A7C5E2;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.mainbottomright{
  width: 2.2rem;
  height: 2rem;
  border: 1px solid #A7C5E2;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  right: 29px;
  display: flex;
}
.topbox{
  flex: 3;
  border-bottom: 1px solid #A7C5E2;
  font-size: .16rem;
  padding-left: 10px;
  line-height: .3rem;
  background-color: #EFF5FB;
  color: #1E5494;
}
.middlebox{
  flex: 7;
  border-bottom: 1px solid #A7C5E2;
  display: flex;
  position: relative;
  flex-direction: column;
  font-size: .16rem;
}
.bottombox{
  flex: 10;
  font-size: .16rem;
}
.middlebox>img{
  height: .5rem;
  position: absolute;
  top: .1rem;
  left: .1rem;
}
.middlebox>div,
.middlebox>span{
  flex: 1;
  padding-left: 1rem;
}
.middlebox>div{
  padding-top: .1rem;
}
.middlebox>span{
  color: #798699;
}
.bottombox>ul li{
  float: left;
  width: 50%;
  height: .33rem;
  line-height: .33rem;
  padding-left: .5rem;
  box-sizing: border-box;
}
.content::after{
  content: '-';
  padding-left: .05rem;
}
.footer{
  font-size: .14rem;
  margin: .2rem 0 0 .1rem;
}
.footer>ul li{
  float: left;
  color: #1E5494;
  cursor: pointer;
}
.footer>ul li:hover{
  text-decoration: underline;
}
.footer>ul li span{
  margin: 0 .08rem 0 .08rem;
  color: #000;
}
.mainbottomright{
  font-size: .16rem;
  padding: .05rem 0 0 .1rem;
  box-sizing: border-box;
}
.skillbox{
  height: 1.2rem;
  width: 2rem;
}
.skillbox div:hover{
  background-color: #F5F5F5;
  cursor: pointer;
}
</style>


