<template>
  <div>
    <el-row class="header">
      <el-col :span="16" class="userinfo">
        <div class="logoimg">
          <img src="https://rescdn.qqmail.com/zh_CN/htmledition/images/logo/2019-ncov/logo_0_04bae87.png" alt="">
        </div>
        <div class="userinfobox">
          <div style="flex:1">
            <div style="font-size:.14rem;float: left;">子君</div>
            <span class="useraddress">wufuguo0213@qq.com</span>
          </div>
          <div style="flex:1">
            <a href="javascript:;" class="indexbtn" @click="showmain(0)">邮箱首页</a>
            <a href="javascript:;" class="settingbtn">设置</a>
            <a href="javascript:;" class="colthbtn">换肤</a>
          </div>
        </div>
      </el-col>
      <el-col :span="8" class="searchbox">
        <div>
          <a href="javascript:;" class="escbtn">退出</a>
          <a href="javascript:;" class="helpbtn">帮助中心</a>
          <a href="javascript:;" class="suggestbtn">反馈建议</a>
          <a class="tophonebtn"><i class="el-icon-mobile-phone"></i></a>
        </div>
        <div>
          <input type="search" placeholder="邮件搜索..." id="" class="topsearch">
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'myheader',
  data () {
    return {
      topsearchvalue:'',
    }
  },
  methods:{
    showmain:function(inx){
      this.$parent.writing(inx);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
  color: #1E5494;
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
.header{
  height: .66rem;
  width: 100%;
  padding-left: .05rem;
  box-sizing: border-box;
}
.logoimg{
  height: .66rem;
  float: left;
}
.logoimg>img{
  height: .6rem;
  width: 2rem;
  float: left;
  margin-left: .1rem;
}
.userinfo{
  height: .66rem;
}
.userinfobox{
  height: .36rem;
  margin-top: .1rem;
  /* width: 3rem; */
  display: flex;
  flex-direction: column;
  padding-top: 0.02rem;
  float: left;
}
.useraddress{
  font-size: .12rem;
  float: left;
}
.useraddress::before{
  content: '<';
}
.useraddress::after{
  content: '>';
}
.indexbtn,
.settingbtn,
.colthbtn{
  font-size: .14rem;
  float: left;
}
.settingbtn,
.colthbtn{
  margin-left: .05rem;
}
.settingbtn::before{
  content: '|';
  margin-right: .05rem;
}
.settingbtn::after{
  content: '-';
  margin-left: .05rem;
}
.searchbox{
  height: .66rem;
  background: url(https://rescdn.qqmail.com/zh_CN/htmledition/images/skin_0/top_bg1e9c5d.jpg) top right no-repeat;
  padding: .1rem .1rem 0 0;
  box-sizing: border-box;
  position: relative;
}
.tophonebtn,
.suggestbtn,
.helpbtn,
.escbtn{
  font-size: .14rem;
  float: right;
}
.suggestbtn,
.helpbtn,
.escbtn{
  margin-left: .05rem;
}
.tophonebtn::after,
.suggestbtn::after,
.helpbtn::after{
  content: '|';
  margin-left: .05rem;
}
.topsearch{
  height: .25rem;
  position: absolute;
  right: 0;
  bottom: .02rem;
  width: 2.5rem;
  font-size: .12rem;
  padding-left: .05rem;
}

</style>


