<template>
  <div>
    <!-- header组件 -->
    <myheader></myheader>
    <!-- 左导航栏组件 -->
    <div class="box">
      <div class="lfetbar">
        <div class="lefttop">
          <ul>
            <li @click="writing(1)">
              <i class="el-icon-edit-outline"></i>
              <a href="javascript:;">写信</a>
            </li>
            <li @click="writing(2)">
              <i class="el-icon-folder-checked"></i>
              <a href="javascript:;">收信</a>
            </li>
            <li @click="writing(3)">
              <i class="el-icon-notebook-1"></i>
              <a href="javascript:;">通讯录</a>
            </li>
          </ul>
        </div>
        <div class="leftbottom">
          <ul>
              <li>
                <a href="javascript:;">收件箱{{"("+recivevalue+")"}}</a>
              </li>
              <li>
                <a href="javascript:;">星标邮件<i class="el-icon-star-on" style="font-size:.20rem;color:#FEDA64"></i></a>
              </li>
              <li>
                <a href="javascript:;">群邮件</a>
              </li>
              <li>
                <a href="javascript:;">草稿箱</a>
              </li>
              <li>
                <a href="javascript:;">已发送</a>
              </li>
              <li>
                <a href="javascript:;">已删除</a>
              </li>
              <li>
                <a href="javascript:;">垃圾箱{{"("+rubbishvalue+")"}}</a>
              </li>
              <li>
                <a href="javascript:;">邮件订阅</a>
              </li>
              <li>
                <a href="javascript:;">我的文件夹</a>
              </li>
              <li>
                <a href="javascript:;">其他邮箱</a>
              </li>
              <li>
                <a href="javascript:;">日历<span>记事本</span></a>
              </li>
              <li>
                <a href="javascript:;">在线文档</a>
              </li>
              <li>
                <a href="javascript:;">附件收藏</a>
              </li>
              <li>
                <a href="javascript:;">文件中转站</a>
              </li>
              <li>
                <a href="javascript:;">贺卡<span>记事本</span></a>
              </li>
              <li>
                <a href="javascript:;">阅读空间</a>
              </li>
          </ul>
        </div>
      </div>
      <!-- main页面 -->
      <div class="mainbox">
        <!-- ////////////////////////////////////////////////////// -->
        <mymain v-show="flaglist[0].flag"></mymain>
        <write v-show="flaglist[1].flag"></write>
        <revice v-show="flaglist[2].flag"></revice>
        <book v-show="flaglist[3].flag"></book>
        <!-- ////////////////////////////////////////////////////// -->
      </div>
    </div>
  </div>
</template>

<script>
import myheader from './myheader'
import mymain from './mymain'
import write from './write'
import revice from './revice'
import book from './book'

export default {
  name: 'index',
  components:{myheader,mymain,write,revice,book},
  data () {
    return {
      topsearchvalue:'',
      recivevalue:10,
      rubbishvalue:12,
      screenWidth: document.documentElement.clientWidth,
      activeName: 'first',
      flaglist: [{
        flag:true,
        key:1,
      },{flag:false,
        key:2,},
        {flag:false,
        key:3,},
        {flag:false,
        key:4,}],
    }
  },
  methods:{
    writing:function(inx){
      for(var i=0;i<this.flaglist.length;i++){
        this.flaglist[i].flag = false;
      }
      this.flaglist[inx].flag = true;
    },
    
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lfetbar{
  width: 1.8rem;
  border-radius: 10px;
  border-right: 5px solid #89B5E9;
  border-bottom: 5px solid #89B5E9;
}
.lfetbar a:hover{
  text-decoration: none;
}
.box{
  width: 100%;
  margin-left: .2rem;
  border-top: 5px solid #89B5E9;
  border-left: 5px solid #89B5E9;
  border-radius: 10px;
  position: relative;
}
.lefttop{
  width: 100%;
  border-bottom: 5px solid #89B5E9;
  padding: .1rem 0 .1rem .1rem;
  box-sizing: border-box;
}
.lefttop>ul li{
  height: .3rem;
  width: 90%;
  font-size: .16rem;
  line-height: .3rem;
  padding-left: .1rem;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}
.lefttop>ul li:hover,
.leftbottom>ul li:hover{
  background-color: #E0ECF9;
}
.lefttop>ul li a{
  font-weight: bold;
  color: #1E5494;
  margin-left: .05rem;
}
.leftbottom>ul li{
  font-size: .14rem;
  height: .3rem;
  line-height: .3rem;
  padding-left: .2rem;
  cursor: pointer;
}
.leftbottom>ul li a{
  color: #1E5494;
}
.leftbottom>ul li:nth-child(16){
  margin-bottom: .2rem;
}
.leftbottom>ul li:nth-child(1),
.leftbottom>ul li:nth-child(7){
 font-weight: bold;
}
.leftbottom>ul li:nth-child(8),
.leftbottom>ul li:nth-child(10){
  border-bottom: 1px solid #F2F4F6;
}
.leftbottom>ul li:nth-child(11) span::before,
.leftbottom>ul li:nth-child(15) span::before{
  content: '|';
  margin: 0 5px 0 5px;
}
.mainbox{
  width: 86%;
  position: absolute;
  left: 1.85rem;
  top: 0;
}
/* ////////////////////////////////////////////////////// */

</style>




