                <button @click="showmain(0)">关闭</button>
                <span @click="havefun"><i class="el-icon-loading"></i> 写累了？休息会...</span>
              </div>
          </div>
          <div class="writebox">
            <div class="writeleft">
              <div class="recipient">
                收件人
                <input type="text" name="" id="1">
              </div>
              <div class="copyto">
                <a href="javascript:;">添加抄送</a>
                <span> - </span>
                <a href="javascript:;">添加密送</a>
                <span> | </span>
                <a href="javascript:;">分别抄送</a>
              </div>
              <div class="theme">
                主题
                <input type="text" name="" id="2">
              </div>
              <div class="accessory">
                <el-upload
                  class="upload-demo"
                  action="https://jsonplaceholder.typicode.com/posts/"
