                  :limit="3">
                  <el-button size="mini" type="primary" class="upbtn">添加附件</el-button>
                  <el-button size="mini" type="primary" class="upbtn">照片</el-button>
                </el-upload>
              </div>
              <div class="textpart">
                <span>正文</span>
                <textarea></textarea>
              </div>
              <div class="sender">
                发件人：
                <span>子君</span>
                <span class="senderid">wufuguo0213@qq.com</span>
              </div>
              <div class="bottombtnbox">
                <div class="btnbox" style="width:117.5%">
                <button >发送</button>
                <button >定时发送</button>
                <button >存草稿</button>
                <button  @click="showmain(0)">关闭</button>
              </div>
              </div>
            </div>
            <div class="writeright">
              <span>通讯录</span>
              <input type="search" placeholder="搜索联系人" id="3">
              <p>我的好友</p>
              <div class="friendlistbox">
                <tree></tree>
              </div>
            </div>
          </div>
          </div>
          <!-- ///////////////////////////////////////////////////// -->
          <div class="writemainbox" v-show="groupflag">
