              <div class="btnbox">
                <button >发送</button>
                <button >存草稿</button>
                <button @click="showmain(0)">关闭</button>
                <span @click="havefun"><i class="el-icon-loading"></i> 写累了？休息会...</span>
              </div>
          </div>
          <div class="writebox">
            <div class="writeleft">
              <div class="recipient">
                我的群
                <input type="text" name="" id="3">
              </div>
              <div class="copyto">
                <a href="javascript:;">添加抄送</a>
