                <a href="javascript:;">添加密送</a>
                <span> | </span>
                <a href="javascript:;">分别抄送</a>
              </div>
              <div class="theme">
                主题
                <input type="text" name="" id="4">
              </div>
              <div class="accessory">
                <el-upload
                  class="upload-demo"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  multiple
                  :limit="3">
                  <el-button size="mini" type="primary" class="upbtn">添加附件</el-button>
                  <el-button size="mini" type="primary" class="upbtn">照片</el-button>
                </el-upload>
              </div>
              <div class="textpart">
                <span>正文</span>
                <textarea></textarea>
              </div>
              <div class="sender">
                发件人：
                <span>子君</span>
                <span class="senderid">wufuguo0213@qq.com</span>
              </div>
              <div class="bottombtnbox">
                <div class="btnbox" style="width:117.5%">
                <button >发送</button>
                <button >存草稿</button>
                <button  @click="showmain(0)">关闭</button>
              </div>
              </div>
            </div>
