              <div class="friendlistbox" style="height:3.9rem">
                <ul class="groupli">
                  <li v-for="(n,inx) in grouplist" :key="inx">{{n.name}}</li>
                </ul>
              </div>
            </div>
          </div>
          </div> 
          <!-- ////////////////////////////////////////////////////////// -->
        </div>
</template>

<script>
import tree from './tree'

export default {
    name:'write',
    components:{tree},
    data() {
        return {
            ordinaryflag: true,
            groupflag: false,
            grouplist:[{
              name:'只有我想赢的队伍'
            },{
              name:'419'
            },{
              name:'大学物理计算机'
            },{
              name:'重庆邮电大学'
            },{
              name:'offer降落伞'
            },{
              name:'兼职群'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },{
              name:'。。。'
            },]
        }
    },
    methods:{
        changordinary:function(){
          var e1 = document.querySelector('.ordinarymail');
          var e2 = document.querySelector('.groupmail');
          e1.style.backgroundColor = '#C1D9F3';
          e2.style.backgroundColor = '#E0ECF9';
          this.ordinaryflag = true;
          this.groupflag = false;
        },
        changgroup:function(){
          var e1 = document.querySelector('.ordinarymail');
          var e2 = document.querySelector('.groupmail');
          e2.style.backgroundColor = '#C1D9F3';
          e1.style.backgroundColor = '#E0ECF9';
          this.ordinaryflag = false;
          this.groupflag = true;
        },
        showmain:function(inx){
          this.$parent.writing(inx);
        },
        havefun:function(){
          location.assign('https://music.163.com/')
        },
    }
}
</script>

<style scoped>
a{
  color: #1E5494;
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
.writingbox{
  padding: .1rem  0 0 .1rem;
}
.tabnavbox{
  font-size: .16rem;
  font-weight: bold;
  width: 100%;
  height: .3rem;
}
.tabnavbox a{
  display: block;
  height: .3rem;
  width: 1rem;
  float: left;
  background-color: #E0ECF9;
  border-radius: 5px 5px 0 0;
  text-align: center;
  line-height: .3rem;
}
.groupmail{
  margin-left: 2px;
}
.btnbox{
  height: .34rem;
  width: 80%;
  background-color: #C1D9F3;
  margin-top: 1px;
  font-size: .14rem;
  padding-left: .1rem;
  position: relative;
  box-sizing: border-box;
}
.btnbox button{
  height: .28rem;
  margin: .03rem 0 0 .03rem;
  font-size: .12rem;
}
.btnbox span{
  position: absolute;
  right: .2rem;
  line-height: .34rem;
  cursor: pointer;
}
.writebox{
  background-color: #EFF5FB;
  width: 80%;
  font-size: .12rem;
  position: relative;
}
.writeleft{
  width: 85%;
  height: 5rem;
  display: flex;
  flex-direction: column;
}
.recipient{
  flex: 3;
  line-height: .3rem;
  padding-left: .3rem;
}
.recipient input{
  height: .2rem;
  margin-left: .1rem;
  width: 79%;
}
.theme input{
  height: .2rem;
  margin-left: .23rem;
  width: 79%;
}
.copyto{
  flex: 3;
  padding-left: .8rem;
  line-height: .3rem;
}
.theme{
  flex: 3;
  padding-left: .3rem;
  line-height: .3rem;
}
.accessory{
  flex: 3;
  padding-left: .6rem;
  font-size: .12rem;
}
.textpart{
  flex: 32;
  position: relative;
  padding-left: .8rem;
  width: 79%;
}
.textpart span{
  position: absolute;
  left: .3rem;
}
.textpart textarea{
  width: 96%;
  height: 3.2rem;
}
.sender{
  flex: 3;
  padding-left: .8rem;
  line-height: .3rem;
}
.bottombtnbox{
  flex: 3;
}
.upbtn{
  background: none;
  color: #1E5494;
  height: .3rem;
  border: none;
}
.upbtn:hover{
  text-decoration: underline;
}
.senderid::after{
  content: '>'
}
.senderid::before{
  content: '<'
}
.writeright{
  height: 4.45rem;
  width: 15%;
  position: absolute;
  right: .1rem;
  top: .01rem;
  border: 1px solid #9FB5CC;
}
.writeright span{
  display: block;
  height: .2rem;
  width: 100%;
  background-color: #fff;
  font-size: .14rem;
  font-weight: bold;
  text-align: center;
}
.writeright input{
  width: 90%;
  margin: 0 5% 0 5%;
  padding-left: 5px;
  height: .2rem;
  margin-top: .05rem;
  margin-bottom: .05rem;
}
.writeright p{
  height: .2rem;
  margin: 0;
  padding-left: .1rem;
}
.friendlistbox{
  margin-top: .05rem;
  background-color: #fff;
  height: 3.7rem;
  overflow: hidden;
}
.groupli li{
  width: 100%;
  height: .2rem;
  padding-left: .1rem;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  line-height: .2rem;
  cursor: pointer;
}
.groupli li:hover{
  background-color: rgb(155, 155, 155)!important;
}
.groupli li:nth-child(2n){
  background-color: rgb(235, 235, 235);
}
</style>


