<template>
    <div>
        <el-tree :data="data" :props="defaultProps" ></el-tree>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        data: [ {
          label: '我的好友',
          children: [{
            label: '子君',
          }, {
            label: '活着',
          }, {
            label: '她（xx）',
          }]
        },
        {
          label: '同学',
          children: [{
            label: '张三',
          }, {
            label: '李四',
          }]
        },{
          label: '家人',
          children: [{
            label: 'dad',
          }, {
            label: 'mom',
          }]
        },{
          label: '朋友',
          children: [{
            label: '王麻子',
          }, {
            label: '周二娃',
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    },
    methods: {
      
    }
  };
</script>
<style scoped>
.el-tree-node__label{
    font-size: .1rem!important;
    width: 2rem!important;
}
.el-tree-node__content{
    height: .26rem!important;
}
</style>


