// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {Button,Row,Col,Tabs,TabPane,Upload,Tree  } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './assets/cssrem'
import { Icon,Grid, GridItem } from 'vant'

Vue.use(Button).use(Row).use(Col).use(Tabs).use(TabPane).use(Upload).use(Tree)
Vue.use(Icon ).use(Grid).use(GridItem)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



